import { HttpClient } from '@angular/common/http';
import { Component, Injector, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import CustomStore from 'devextreme/data/custom_store';
import { Subscription } from 'rxjs';
import { Socket } from 'socket.io-client';
import { AppConstants } from '../@core/constants/app-constants';
import { AlertService } from '../@core/services/alert.service';
import { AuthService } from '../@core/services/auth.service';
import { DataStoreService } from '../@core/services/data-store.service';
import { DynamicsearchService } from '../@core/services/dynamicsearch.service';
import { LocalService } from '../@core/services/local.service';
import { PageBuilderAddService } from '../@core/services/page-builder-add.service';
import { PageBuilderService } from '../@core/services/page-builder.service';
@Component({
  selector: 'lib-page-builder',
  encapsulation: ViewEncapsulation.None,
  templateUrl: './page-builder.component.html',
  styleUrls: ['./page-builder.component.scss']
})
export class PageBuilderComponent implements OnInit, OnDestroy {

  pageList: any;
  tableColumns: any[];
  updateGrid: any;
  totalcount = 0;
  orgSubs!: Subscription;
  orgId: any;
  filterOptions: any[];
  filterForm!: UntypedFormGroup;
  gridOptions: { value: string; label: string; code: string }[];
  gridId: any;
  localstore: LocalService;
  alertService: AlertService;
  dataSource: any;
  http: HttpClient;
  storage: LocalService;
  socket: Socket
  pageBuilderServ: PageBuilderService;
  inputValidationMethod: any;
  RBACORG: any;
  constructor(
    injector: Injector,
    public pageBuilderService: PageBuilderService,
    private router: Router,
    private route: ActivatedRoute,
    public auth: AuthService,
    private fb: UntypedFormBuilder,
    private pageBuilderAddService: PageBuilderAddService,
    private dynamicSearchService: DynamicsearchService,
    private _storeservice: DataStoreService
  ) {
    this.localstore = injector.get<LocalService>(LocalService);
    this.alertService = injector.get<AlertService>(AlertService);
    this.pageBuilderServ = injector.get<PageBuilderService>(PageBuilderService);

    this.gridOptions = [
      { label: 'Sorting', value: 'sort', code: '' },
      { label: 'Filtering', value: 'filter', code: '' },
      { label: 'Hide', value: 'hide', code: '' },
      { label: 'Fixed', value: 'fixed', code: '' },
      { label: 'Default Columns', value: 'Choosable', code: '' },
      { label: 'Visible', value: 'visible', code: '' },
      { label: 'Date', value: 'dateFormat', code: '' },
      // { label: 'Mobile', value: 'mobileView', code: '' },
      // { label: 'Tab', value: 'tabView', code: '' }
    ];
    this.gridId = 'page-list-grid';
    this.orgSubs = this._storeservice.currentStore.subscribe((res: any) => {
      if (res) {
        this.inputValidationMethod = res['INPUTVALIDATIONMETHOD'];
        this.RBACORG = res['RBACORG'];
        this.socket = this.RBACORG['socket']
        this.orgId = parseInt(this.RBACORG['orgID']);
        if (this.orgId) {
          this.getPageDesignList(this.pageBuilderService);
        }

      }
    })

  }


  public form: any;
  id: string | null = '';
  onlineUserListSubscription: Subscription;
  updateNotificationSubscription: Subscription;

  ngOnInit(): void {
    this.localstore.setObj('EDITBASICINFO', '');
    this.localstore.setObj('CREATE PAGE', false);
    // $(document).on('click', '.strip_head .dropdown-menu', function (e) {
    //   e.stopPropagation();
    // });
    this.loadFilterForm();
    this.setGridColumns();
    this.getGridConfig();
  }

  ngOnDestroy(): void {
    this.orgSubs.unsubscribe();
  }
  getPageDesignList(http) {
    const data = {};
    function isNotEmpty(value: any): boolean {
      return value !== undefined && value !== null && value !== '';
    }
    const _this = this;
    this.dataSource = new CustomStore({
      load(loadOptions: any) {
        console.log('data');
        ['skip', 'take', 'requireTotalCount', 'requireGroupCount', 'sort', 'filter', 'pageconfig', 'group'].forEach(
          item => {
            if (item in loadOptions && isNotEmpty(loadOptions[item])) {
              data[item] = loadOptions[item];
            } else {
              delete data[item];
            }
          }
        );
        return new Promise((resolve, reject) => {
          let sort = '';
          if (isNotEmpty(data['sort'])) {
            sort = JSON.stringify(data['sort']);
          }
          http.getAllPageDesigns(_this.auth.getOrgID() ? _this.auth.getOrgID() : `${_this.orgId}?returnUserPage=false&skip=${data['skip']}&take=${data['take']}&filter=${data['filter']}&sort=${sort}`)
            .subscribe({
              next: (result) => {
                result.data.data.forEach(page => {
                  page.status = page.isEnable ? 'Active' : 'Inactive';
                });

                const response = {
                  data: result.data.data,
                  totalCount: result.data.totalcount ? result.data.totalcount : 0
                };

                resolve(response);
              },
              error: (error) => {
                reject(error);
              }
            });
        });
      }
    });

    console.log(this.dataSource);
  }

  goToBuilder(): any {
    this.localstore.setObj('SELECTED_PAGE', '');
    this.pageBuilderAddService.setPageType('');
    this.localstore.setObj('PAGEDESIGN_BASICINFO', '');
    this.localstore.setObj('CREATE PAGE', true);
    this.router.navigate(['../add/basic-info'], { relativeTo: this.route });
  }

  setGridColumns() {
    this.tableColumns = [
      {
        columnDef: 'id',
        header: 'Page Id',
        cell: (element: any) => `${element.id}`,
        dateFormat: false,
        icon: false,
        filter: true,
        link: false,
        sort: false,
        hide: false,
        fixed: false,
        Choosable: false,
        visible: false,
        selected: ['filter']
      },
      {
        columnDef: 'activeVersion.pagename',
        header: 'Page Name',
        cell: (element: any) => `${element.activeVersion.pagename}`,
        dateFormat: false,
        icon: false,
        filter: true,
        link: false,
        sort: false,
        hide: false,
        fixed: false,
        Choosable: false,
        visible: false,
        selected: ['filter']
      },
      {
        columnDef: 'pageTypeDetails.value',
        header: 'Page Type',
        cell: (element: any) => `${element.pageTypeDetails.value}`,
        dateFormat: false,
        icon: false,
        filter: true,
        link: false,
        sort: false,
        hide: false,
        fixed: false,
        Choosable: false,
        visible: false,
        selected: ['filter']
      },
      {
        columnDef: 'insertedOn',
        header: 'Created On',
        cell: (element: any) => `${element.insertedOn}`,
        dateFormat: true,
        icon: false,
        filter: true,
        link: false,
        sort: false,
        hide: false,
        fixed: false,
        Choosable: false,
        visible: false,
        selected: ['filter', 'dateFormat']
      },
      {
        columnDef: 'insertedByDetails.firstname',
        header: 'Created By',
        cell: (element: any) => `${element.pageTypeDetails.value}`,
        dateFormat: false,
        icon: false,
        filter: true,
        link: false,
        sort: false,
        hide: false,
        fixed: false,
        Choosable: false,
        visible: false,
        selected: ['filter']
      },
      {
        columnDef: 'activeVersion.description',
        header: 'Description',
        cell: (element: any) => `${element.activeVersion.description}`,
        dateFormat: false,
        icon: false,
        filter: true,
        link: false,
        sort: false,
        hide: false,
        fixed: false,
        Choosable: false,
        visible: false,
        selected: ['filter']
      },
      {
        columnDef: 'status',
        header: 'Status',
        cell: (element: any) => `${element.status}`,
        dateFormat: false,
        icon: false,
        filter: true,
        link: false,
        sort: false,
        hide: false,
        fixed: false,
        Choosable: false,
        visible: true,
        selected: ['filter']
      }
    ];
    this.updateGrid = {
      edit: true,
      view: true,
      lock: true,
      version: true,
      activatePage: true,
      deactivatePage: true,
      duplicate: true,
      delete: false,
      externalLink: false
    };
    this.setFilterOptions(this.tableColumns);
  }

  viewVersions(evt) {
    if (evt) {
      this.router.navigate(['../versions/', evt.data.id], { relativeTo: this.route });
    }
  }

  //   updatePage(_evnet) {
  //     // This is intentional
  //   }

  editTableRow(evt) {
    this.localstore.setObj('EDITBASICINFO', evt.data);
    this.localstore.setObj('PAGEDESIGN_BASICINFO', '');
    const loginUser = sessionStorage.getItem('id');
    console.log(loginUser);
    if (evt.data.locked && Number(loginUser) === evt.data.lockedby) {
      this.router.navigate(['../edit/' + evt.data.id + '/basic-info'], { relativeTo: this.route });
    }
    if (evt.data.locked && Number(loginUser) !== evt.data.lockedby) {
      this.alertService.error(
        'Locked By - ' + (evt.data.lockedbyUser.lastname + ' ' + evt.data.lockedbyUser.firstname)
      );
    } else {
      this.router.navigate(['../edit/' + evt.data.id + '/basic-info'], { relativeTo: this.route });
    }
  }

  viewTableRow(evt) {
    if (evt.data.pageTypeDetails.key == 'BGP') {
      this.router.navigate(['../../dynamic-search/view/', evt.data.id], { relativeTo: this.route });
    } else if (evt.data.pageTypeDetails.key == 'BTP') {
      this.alertService.info('Navigating to tab view...');
      this.router.navigate([`pages/dynamic/tab/${evt.data.id}`]);
    } else {
      this.router.navigate(['../view', evt.data.id], { relativeTo: this.route });
    }
  }
  duplicateRow(evt) {
    this.pageBuilderService.duplicateDesignPage({}, evt.data.id).subscribe(
      response => {
        this.alertService.success(response['data'].pagename + ' created successfully');
        this.getPageDesignList(this.pageBuilderService);
      },
      _error => {
        this.alertService.error(AppConstants.errorMessage);
      }
    );
  }

  loadFilterForm() {
    this.filterForm = this.fb.group({
      selectedFilter: ['']
    });
  }

  getGridConfig() {
    this.dynamicSearchService.getStaticGridConfig(this.gridId).subscribe((res: any) => {
      if (res && res.data?.config) {
        const sampleData = res.data?.config;
        const columnResponseConfig = JSON.parse(sampleData);
        this.tableColumns = columnResponseConfig;
        this.setFilterOptions(columnResponseConfig);
      } else {
        this.setGridColumns();
      }
    });
  }

  setFilterOptions(colConfig) {
    const options = [];
    const arr = [];
    colConfig.map(col => {
      options.push({
        columnName: col.header,
        items: this.gridOptions.map(op => {
          return {
            id: `${col.columnDef}-${op.value}`,
            label: op.label,
            value: op.value,
            code: col.columnDef,
            active: col[op.value] === true ? true : false
          };
        })
      });
      col.selected.map(v => arr.push(`${col.columnDef}-${v}`));
    });
    this.filterOptions = options;
    this.filterForm.patchValue({ selectedFilter: arr });
  }
  changeFilterOptions(e) {
    const [code, value] = e.itemValue.split('-');
    const isExists = this.tableColumns.filter(col => {
      return col.columnDef === code && !!col[value];
    });
    if (isExists.length === 0) {
      this.tableColumns.forEach(tc => {
        tc[value] = tc.columnDef === code ? true : tc[value];
        if (tc.columnDef === code) {
          tc?.selected.push(value);
        }
        return tc;
      });
    } else {
      this.conditionCheckElse(value, code);
    }
    const columnConfig = JSON.stringify(this.tableColumns);
    this.saveGridConfig(columnConfig);
  }
  conditionCheckElse(value, code) {
    this.tableColumns.forEach(c => {
      c[value] = c.columnDef === code ? false : c[value];
      if (c.columnDef === code) {
        c?.selected?.forEach((s, i) => {
          s === value && c?.selected.splice(i, 1);
        });
      }
      return c;
    });
  }
  saveGridConfig(data) {
    const reqBody = {
      type: 'STATIC_GRID',
      mappedid: this.gridId,
      config: data
    };
    this.dynamicSearchService.saveGridUserPreference(reqBody).subscribe(_r => {
      this.getGridConfig();
    });
  }

  activatePage(event) {
    const pageId = event.data.id;
    this.pageBuilderService.activatePage(pageId).subscribe(
      _response => {
        this.alertService.success('Page activated successfully.');
        this.getPageDesignList(this.pageBuilderService);
      },
      _error => {
        this.alertService.error('Failed to activate page. Please try again.');
      }
    );
  }

  deactivatePage(event) {
    const pageId = event.data.id;
    this.pageBuilderService.deactivatePage(pageId).subscribe(
      _response => {
        this.alertService.success('Page deactivated successfully.');
        this.getPageDesignList(this.pageBuilderService);
      },
      _error => {
        this.alertService.error('Failed to deactivate page. Please try again.');
      }
    );
  }

}
