import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { DataStoreService } from '../../../@core/services/data-store.service';
import { PageBuilderVersionURL } from './page-builder-version-url.config';

@Injectable({
  providedIn: 'root'
})
export class PageBuilderService {
  httpService: any;
  constructor(private _storeservice: DataStoreService) {
    this._storeservice.currentStore.subscribe((res: any) => {
      if(res){
        this.httpService = res['HTTPSERVICE']
      }
    })
     // This is intentional
  }

  getVersionList(pageId?: number | string): Observable<any> {
    return this.httpService.get(
      PageBuilderVersionURL.EndPoints.page_config.versionList.replace('{id}', String(pageId))
    );
  }

  activateVersion(versionId?: number | string): Observable<any> {
    return this.httpService.patch(
      PageBuilderVersionURL.EndPoints.page_config.activateVersion.replace('{id}', String(versionId))
    );
  }

  updatePageVersion(versionId?: number | string, formData?: any, createPage?: boolean): Observable<any> {
    const url = PageBuilderVersionURL.EndPoints.page_config.updateVersion.replace('{id}', String(versionId));
    return this.httpService.patch(createPage ? `${url}?updateActiveVersion=true` : `${url}`, formData);
  }

  copyPageVersion(data, id) {
    return this.httpService.post(
      PageBuilderVersionURL.EndPoints.page_config.copyVersion.replace('{id}', String(id)),
      data
    );
  }
}
