import { Injector } from '@angular/core';
import { FormioCustomComponentInfo, registerCustomFormioComponent } from '@formio/angular';
import { RatingWrapperComponent } from './rating-wrapper.component';

const COMPONENT_OPTIONS: FormioCustomComponentInfo = {
  type: 'myrating',
  selector: 'my-rating',
  title: 'Rating',
  group: 'basic',
  icon: 'fa fa-star'
  // editForm: minimalEditForm,
  // template: 'dateTime'
};

function _minimalEditForm() {
  return {
    components: [
      { key: 'type', type: 'hidden' },
      {
        weight: 0,
        type: 'textfield',
        input: true,
        key: 'label',
        label: 'Label',
        placeholder: 'Label',
        validate: {
          required: true
        }
      },
      {
        weight: 10,
        type: 'textfield',
        input: true,
        key: 'key',
        label: 'Field Code',
        placeholder: 'Field Code',
        tooltip: 'The code/key/ID/name of the field.',
        validate: {
          required: true,
          maxLength: 128,
          pattern: '[A-Za-z]\\w*',
          patternMessage:
            'The property name must only contain alphanumeric characters, underscores and should only be started by any letter character.'
        }
      },
      {
        weight: 20,
        type: 'textfield',
        input: true,
        key: 'customOptions.myOption',
        label: 'My Custom Option',
        placeholder: 'My Custom Option',
        validate: {
          required: true
        }
      }
    ]
  };
}

export function registerRatingComponent(injector: Injector) {
  console.log('registerPopupComponent called...');
  try {
    registerCustomFormioComponent(COMPONENT_OPTIONS, RatingWrapperComponent, injector);
    console.log('registerPopupComponent complete...');
  } catch (err) {
    console.error('error occ in registercomp', err);
  }
}
