import { Injector } from '@angular/core';
import { FormioCustomComponentInfo, registerCustomFormioComponent } from '@formio/angular';
import { GlobalSearchWrapperComponent } from './global-search-wrapper.component';
// import { environment } from '@env/environment';


const COMPONENT_OPTIONS: FormioCustomComponentInfo = {
  type: 'globalsearch',
  selector: 'app-globalserach',
  title: 'Global Search',
  group: 'basic',
  icon: 'fa fa-search',
  editForm: globalSearchForm,
  fieldOptions: ['schema', 'table', 'columns', 'responseColumns', 'label']
};

export function globalSearchForm() {
  const oid = sessionStorage.getItem('orgid');
  const environment: any = {};
  console.log(oid);
  return {
    components: [
      {
        key: 'type',
        type: 'hidden'
      },
      {
        weight: 0,
        type: 'textfield',
        input: true,
        key: 'label',
        label: 'Label',
        placeholder: 'Label',
        validate: {
          required: true
        }
      },
      {
        weight: 10,
        label: 'Schema',
        widget: 'choicesjs',
        tableView: true,
        dataSrc: 'url',
        data: {
          url: `${environment.apiHost}/commonsearch/schemas/${oid}`,
          headers: [
            {
              key: 'authorization',
              value: 'Bearer {{sessionStorage.getItem(\'jwt-token\').toString()}}'
            }
          ]
        },
        valueProperty: 'name',
        template: '<span>{{ item.name }}</span>',
        key: 'schema',
        type: 'select',
        input: true,
        selectValues: 'data',
        disableLimit: false,
        placeholder: 'Select Schema',
        validate: {
          required: false
        }
      },
      {
        weight: 10,
        label: 'Tables',
        widget: 'choicesjs',
        tableView: true,
        dataSrc: 'url',
        data: {
          url: `${environment.apiHost}/commonsearch/tables/{{data[\'schema\']}}`,
          headers: [
            {
              key: 'authorization',
              value: 'Bearer {{sessionStorage.getItem(\'jwt-token\').toString()}}'
            }
          ]
        },
        valueProperty: 'name',
        template: '<span>{{ item.name }}</span>',
        refreshOn: 'schema',
        key: 'table',
        type: 'select',
        input: true,
        selectValues: 'data',
        disableLimit: false,
        placeholder: 'Select Table',
        validate: {
          required: false
        }
      },
      {
        weight: 10,
        label: 'Columns',
        widget: 'choicesjs',
        tableView: true,
        multiple: true,
        dataSrc: 'url',
        data: {
          url: `${environment.apiHost}/commonsearch/columns/{{data[\'table\']}}/{{data[\'schema\']}}`,
          headers: [
            {
              key: 'authorization',
              value: 'Bearer {{sessionStorage.getItem(\'jwt-token\').toString()}}'
            }
          ]
        },
        valueProperty: 'name',
        template: '<span>{{ item.name }}</span>',
        refreshOn: 'table',
        key: 'columns',
        type: 'select',
        selectValues: 'data',
        disableLimit: false,
        input: true,
        placeholder: 'Select columns',
        validate: {
          required: false
        }
      },
      {
        weight: 10,
        label: 'Response Columns',
        widget: 'choicesjs',
        tableView: true,
        multiple: true,
        dataSrc: 'url',
        data: {
          url: `${environment.apiHost}/commonsearch/columns/{{data[\'table\']}}/{{data[\'schema\']}}`,
          headers: [
            {
              key: 'authorization',
              value: 'Bearer {{sessionStorage.getItem(\'jwt-token\').toString()}}'
            }
          ]
        },
        valueProperty: 'name',
        template: '<span>{{ item.name }}</span>',
        refreshOn: 'table',
        key: 'responseColumns',
        type: 'select',
        selectValues: 'data',
        disableLimit: false,
        input: true,
        placeholder: 'Select Response Columns',
        validate: {
          required: false
        }
      },
      {
        weight: 10,
        type: 'textfield',
        input: true,
        key: 'key',
        label: 'API Key',
        placeholder: 'Field Code',
        tooltip: 'The code/key/ID/name of the field.',
        validate: {
          required: true,
          maxLength: 128,
          pattern: '[A-Za-z]\\w*',
          patternMessage:
            'The property name must only contain alphanumeric characters, underscores and should only be started by any letter character.'
        }
      }
    ]
  };
}

export function registerGlobalSearchComponent(injector: Injector) {
  console.log('Register global search component called...');
  try {
    registerCustomFormioComponent(COMPONENT_OPTIONS, GlobalSearchWrapperComponent, injector);
    console.log('Global search component registered');
  } catch (error) {
    console.log(error, 'Error in registering Global Search');
  }
}
