import { Injector } from '@angular/core';
import { FormioCustomComponentInfo, registerCustomFormioComponent } from '@formio/angular';
import { DateRangeWrapperComponent } from './dateRange-wrapper.component';

const COMPONENT_OPTIONS: FormioCustomComponentInfo = {
  type: 'dateWithRange',
  selector: 'date-angular',
  title: 'Date Range Angular',
  group: 'advanced',
  icon: 'calendar',
  editForm: minimalEditForm,
  fieldOptions: ['startDateKey', 'endDateKey'],
  schema: {
    validate: {
      required: true
    }
  }
};

export function minimalEditForm() {
  return {
    components: [
      { key: 'type', type: 'hidden' },
      {
        weight: 0,
        type: 'textfield',
        input: true,
        key: 'label',
        label: 'Label',
        placeholder: 'Label',
        validate: {
          required: true
        }
      },
      {
        weight: 10,
        type: 'textfield',
        input: true,
        key: 'key',
        label: 'API Key',
        placeholder: 'Field Code',
        tooltip: 'The code/key/ID/name of the field.',
        validate: {
          required: true,
          maxLength: 128,
          pattern: '[A-Za-z]\\w*',
          patternMessage:
            'The property name must only contain alphanumeric characters, underscores and should only be started by any letter character.'
        }
      },
      {
        weight: 20,
        type: 'textfield',
        input: true,
        key: 'startDateKey',
        label: 'Start Date Key',
        placeholder: 'Start Date Key',
        tooltip: 'The code/key/ID/name of the start date.',
        validate: {
          required: true,
          maxLength: 128,
          pattern: '[A-Za-z]\\w*',
          patternMessage:
            'The property name must only contain alphanumeric characters, underscores and should only be started by any letter character.'
        }
      },
      {
        weight: 30,
        type: 'textfield',
        input: true,
        key: 'endDateKey',
        label: 'End Date Key',
        placeholder: 'End Date Key',
        tooltip: 'The code/key/ID/name of the end date.',
        validate: {
          required: true,
          maxLength: 128,
          pattern: '[A-Za-z]\\w*',
          patternMessage:
            'The property name must only contain alphanumeric characters, underscores and should only be started by any letter character.'
        }
      },
      {
        weight: 413,
        type: 'checkbox',
        input: true,
        key: 'validate.required',
        label: 'Required'
      }
    ]
  };
}

export function registerDateRangeComponent(injector: Injector) {
  console.log('registerDateRangeComponent called...');
  try {
    registerCustomFormioComponent(COMPONENT_OPTIONS, DateRangeWrapperComponent, injector);
    console.log('registerDateRangeComponent complete...');
  } catch (err) {
    console.error('error occ in registercomp', err);
  }
}
