import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormioCustomComponent, FormioEvent } from '@formio/angular';
import { DataStoreService } from '../../../@core/services/data-store.service';

export interface SearchCriteria {
  schema: any;
  table: any;
  responseColumns: any;
  columns: any;
  value: string;
}

@Component({
  selector: 'app-global-search-wrapper',
  templateUrl: './global-search-wrapper.component.html',
  styleUrls: ['./global-search-wrapper.component.scss']
})
export class GlobalSearchWrapperComponent implements FormioCustomComponent<SearchCriteria>, OnInit {
  @Input()
  value: SearchCriteria;

  @Output()
  valueChange = new EventEmitter<SearchCriteria>();

  @Input()
  disabled = false;

  @Input()
  schema: any;

  @Input()
  table: any;

  @Input()
  columns: any;

  @Input()
  responseColumns: any;

  @Output()
  formioEvent = new EventEmitter<FormioEvent>();

  searchInput: string;
  searchResults: any = [];
  columnHeaders: any = [];
  cols: any = [];
  httpService: any;

  constructor(private _storeservice: DataStoreService) {
    this._storeservice.currentStore.subscribe((res: any) => {
      if (res) {
        this.httpService = res['HTTPSERVICE']
      }
    })
  }

  ngOnInit(): void {
    console.log('should not be empty');
  }

  searchElement() {
    this.cols = [];
    const search: SearchCriteria = {
      schema: this.schema,
      table: this.table,
      columns: this.columns,
      value: this.searchInput,
      responseColumns: this.responseColumns
    };
    this.httpService.post('/commonsearch', search).subscribe((res: any) => {
      this.searchResults = res.data;
      this.columnHeaders = Object.keys(res.data[0]);
      this.columnHeaders.map(h => {
        this.cols.push({ field: h, header: h });
      });
    });
  }
}
