import { Injector } from '@angular/core';
import { FormioCustomComponentInfo, registerCustomFormioComponent } from '@formio/angular';
import { FileUploadWrapperComponent } from './file-upload-wrapper.component';

const COMPONENT_OPTIONS: FormioCustomComponentInfo = {
  type: 'fileupload',
  selector: 'app-fileupload',
  group: 'basic',
  title: 'File Upload',
  icon: 'file'
};

export function registerFileUploadComponent(injector: Injector) {
  console.log('registerFileUploadComponent called...');
  try {
    registerCustomFormioComponent(COMPONENT_OPTIONS, FileUploadWrapperComponent, injector);
    console.log('File Upload Component Registration Done');
  } catch (error) {
    console.log('Error is register File upload component');
  }
}
