import { Injector } from '@angular/core';
import { FormioCustomComponentInfo, registerCustomFormioComponent } from '@formio/angular';
import { FileUploadBasicComponent } from './file-upload-basic.component';

const COMPONENT_OPTIONS: FormioCustomComponentInfo = {
  type: 'basicfileupload',
  selector: 'app-basicfileupload',
  group: 'basic',
  title: 'Basic File Upload',
  icon: 'file'
};

export function registerBasicFileUploadComponent(injector: Injector) {
  console.log('registerBasicFileUploadComponent called...');
  try {
    registerCustomFormioComponent(COMPONENT_OPTIONS, FileUploadBasicComponent, injector);
    console.log('Basic File Upload Component Registration Done');
  } catch (error) {
    console.log('Error is register Basic File upload component');
  }
}
