import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormioCustomComponent, FormioEvent } from '@formio/angular';
import { ConfirmationService } from 'primeng/api';
import { AlertService } from '../../../@core/services/alert.service';
import { AttachmentsService } from '../../../@core/services/attachments.service';
import { AuthService } from '../../../@core/services/auth.service';
import { DataStoreService } from '../../../@core/services/data-store.service';
import { PageBuilderAddService } from '../../../@core/services/page-builder-add.service';

export interface FileUpload {
  fileName: string;
  contentType: string;
  type: string;
}
export interface FileUploadInfo {
  type: string;
  formid: number;
  attachmentdetails: any[];
}
@Component({
  selector: 'app-file-upload-wrapper',
  templateUrl: './file-upload-wrapper.component.html',
  styleUrls: ['./file-upload-wrapper.component.scss'],
  providers: [ConfirmationService]
})
export class FileUploadWrapperComponent implements FormioCustomComponent<any>, OnInit {
  @Input()
  value: any;
  @Output()
  valueChange = new EventEmitter<any>();
  @Input()
  disabled = false;
  @Output()
  formioEvent = new EventEmitter<FormioEvent>();
  uploadedFiles: any[] = [];
  formStatus: string;
  sharedInfo: any = {};
  pageId: any;
  responseId: any;
  uploadedFilesTest: any = [];
  tableColumns: any[];
  updateGrid: any;
  categoryList: any = [];
  subCategoryList: any = [];
  category: string;
  subCategory: string;
  lookupList: any = [];
  categoryid: any;
  subcategoryid: any;
  httpService: any;
  constructor(
    private _storeservice: DataStoreService,
    private alertService: AlertService,
    private authService: AuthService,
    private pageBuilderAddService: PageBuilderAddService,
    private confirmationService: ConfirmationService,
    private attachmentsService: AttachmentsService
  ) {
    this.formStatus = 'FORM_RESPONSE_ATTACHMENTS_';
    this.sharedInfo = this.authService.getSharedMessage();
    this.pageId = this.sharedInfo?.pageId;
    this.responseId = this.sharedInfo?.id;
    this._storeservice.currentStore.subscribe((res: any) => {
      if (res) {
        this.httpService = res['HTTPSERVICE']
      }
    })
  }
  ngOnInit() {
    this.setTableColumns();
    this.getCategory();
    if (this.responseId) {
      this.formStatus = `${this.formStatus}UPDATE`;
    } else {
      this.formStatus = `${this.formStatus}CREATE`;
    }
    this.uploadedFiles = [];
    if (this.responseId) {
      this.getAllAttachments();
    }
  }
  onUpload(event, upload) {
    let resID = '';
    if (this.responseId) {
      resID = `/${this.responseId}`;
    }
    let data: FileUpload;
    event.files.map(f => {
      data = {
        fileName: `${this.pageId}${resID}/${f.name}`,
        contentType: f.type,
        type: this.formStatus
      };
      this.pageBuilderAddService.getUploadKey(data).subscribe((res: any) => {
        const url = res.data;
        const uploadDetails = f;
        const type = f.type;
        const fileURL = res.data.split('?')[0];
        this.uploadedFiles.push({
          name: f.name,
          path: fileURL,
          attachmenttype: f.type,
          category: this.categoryid[0].id,
          subcategory: this.subcategoryid[0].id,
          categoryName: this.category,
          subcategoryName: this.subCategory
        });
        const fileInfo: FileUploadInfo = {
          type: this.formStatus,
          formid: Number(this.pageId),
          attachmentdetails: this.uploadedFiles
        };
        this.httpService.putUpload(url, uploadDetails, type).subscribe(() => {
          this.alertService.success('Uploaded Successfully!');
          this.value = fileInfo;
          this.valueChange.emit(fileInfo);
          this.formioEvent.emit({ eventName: 'customEvent', data: { value: { fileInfo }, type: 'fileUpload' } });
          this.setTableColumns();
          upload.clear();
          this.subCategory = '';
          this.category = '';
        });
        this.authService.setSharedMessage(fileInfo);
      });
    });
  }
  getAllAttachments() {
    this.pageBuilderAddService.getFormResponseAttachment(this.pageId, this.responseId).subscribe(res => {
      const data = res['data'];
      const fileInfo: FileUploadInfo = {
        type: this.formStatus,
        formid: Number(this.pageId),
        attachmentdetails: data
      };
      this.authService.setSharedMessage(fileInfo);
      this.uploadedFiles = data.map(f => {
        return {
          id: f.id,
          name: f.name,
          path: f.path,
          categoryName: f.categoryDetails.key,
          subcategoryName: f.subcategoryDetails.key,
          attachmenttype: f.attachmenttype,
          category: f.category,
          subcategory: f.subcategory
        };
      });
    });
  }
  deleteAttachment(e) {
    const file = e?.data;
    if (file?.id) {
      this.pageBuilderAddService.deleteFormResponseAttachment(file.id).subscribe(() => {
        this.alertService.success('Deleted successfully!');
        this.getAllAttachments();
      });
    } else {
      this.uploadedFiles.forEach((f, i) => {
        if (f.path === file.path) {
          this.uploadedFiles.splice(i, 1);
          this.setTableColumns();
        }
      });
    }
  }
  downloadAttachment(event) {
    const path = event.data.path.split('/');
    const path1 = path.pop();
    const path2 = path.pop();
    const path3 = path.pop();
    const path4 = path.pop();
    const data = {
      fileName: `${path4}/${path3}/${path2}/${path1}`
    };
    this.pageBuilderAddService.downloadFormResponseAttachment(data).subscribe((res: any) => {
      if (res && res.status == 'success') {
        window.open(res.data, '_blank');
      }
    });
  }
  confirm(event) {
    this.confirmationService.confirm({
      message: 'Are you sure that you want to perform this action?',
      accept: () => {
        this.deleteAttachment(event);
      }
    });
  }
  setTableColumns() {
    this.tableColumns = [
      // {
      //   columnDef: 'id',
      //   header: 'Id',
      //   cell: (element: any) => `${element.id}`,
      //   dateFormat: false,
      //   icon: false,
      //   filter: false,
      //   link: false
      // },
      {
        columnDef: 'name',
        header: 'File Name',
        cell: (element: any) => console.log(element, 'elementssss'),
        dateFormat: false,
        icon: false,
        filter: false,
        link: false
      },
      {
        columnDef: 'categoryName',
        header: 'Category',
        dateFormat: false,
        icon: false,
        filter: false,
        link: false
      },
      {
        columnDef: 'subcategoryName',
        header: 'Sub Category',
        dateFormat: false,
        icon: false,
        filter: false,
        link: false
      },
      {
        columnDef: 'attachmenttype',
        header: 'Type',
        cell: (element: any) => `${element.type}`,
        dateFormat: false,
        icon: false,
        filter: false,
        link: false
      }
    ];
    this.updateGrid = {
      edit: false,
      view: false,
      lock: false,
      duplicate: false,
      delete: false,
      externalLink: false,
      showDownload: true,
      showDelete: true,
      isNewlyUploaded: this.responseId ? false : true
    };
  }
  getCategory() {
    const categoryname = 'DOCUMENT_LIST';
    this.attachmentsService.getCategoryLookup(categoryname).subscribe((res: any) => {
      this.lookupList = res.data;
      this.lookupList.map(z => {
        if (!z.parentid) {
          this.categoryList.push(z);
        }
      });
    });
  }
  getSubCategory() {
    this.subcategoryid = this.subCategoryList.filter(e => e.lookupvalue === this.subCategory);
  }
  getCatogoryItem() {
    const data = this.lookupList.filter(x => x.lookupvalue === this.category);
    this.categoryid = data;
    this.subCategoryList = this.lookupList.filter(y => y.parentid === data[0].id);
  }
}
